<template>
  <div>
    <van-image class="imgs" :src="imgurl" />

    <van-row type="flex" justify="space-around" align="center" class="top2">
      <van-col class="txt mm" span="6">
        <van-button plain class="btnjian" size="mini" type="primary" round>
          <van-icon @click="back" name="arrow-left" />
        </van-button>
      </van-col>
      <van-col span="4" class="txt">
        <van-button plain class="btn" type="primary" size="mini" round @click="log">已有账号?</van-button>
      </van-col>
    </van-row>
    <van-row type="flex" justify="space-around" align="center" class="top3">
      <van-col span="24" class="zhong">Annasui安娜账号可登录Annasui香水</van-col>
    </van-row>
    <van-form @submit="onSubmit">
      <van-field
        v-model="ruleForm.username"
        name="username"
        label="用户名"
        placeholder="用户名/手机号码"
        native-type="check"
        @change="check"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        :rules="[{ required: true, message: '请填写密码' }]"
        v-model="ruleForm.password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
      />
      <div style="margin: 16px;">
        <van-button block color="#b0b0b0" type="info"  native-type="submit">注册</van-button>
        <van-row type="flex" justify="space-between" align="center" class="top1 wei">
          <van-col span="5" class="txt zz">已有账号?</van-col>
        </van-row>
      </div>
    </van-form>
  </div>
</template>
<script>
import Vue from "vue";
import {
  Image as VanImage,
  Col,
  Row,
  Icon,
  Form,
  Area,
  Grid,
  GridItem,
  Toast,
} from "vant";
Vue.use(Col);
Vue.use(Row);
Vue.use(Icon);
Vue.use(Form);
Vue.use(VanImage);
Vue.use(Area);
Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Toast);
export default {
  name: "Login",
  data() {
    return {
      canReg: true,
      ruleForm: {
        username: "",
        password: "",
      },
      imgurl: "/img1/log.jpg",
      imgt: "img/0ae94014fe18482c952594bbc5167fb7.jpg",
      imglist: [
        {
          text: 1,
          imgs: "/img/0ae94014fe18482c952594bbc5167fb7.jpg",
        },
        {
          text: 1,
          imgs: "/img/0d2bde96cba344f9a46b66c0f8b9f63d.jpg",
        },
        {
          text: 1,
          imgs: "/img/f342fb49e3804aa8a41f1055a2d932e3.jpg",
        },
        {
          text: 1,
          imgs: "/img/febdf16b53f84fd78310858846c3151c.jpg",
        },
      ],
    };
  },
  methods: {
    huan() {
      console.log(123);
    },
    back() {
      console.log(123);
      window.history.go(-1);
    },
    //跳到登录
    log() {
      this.$router.push({
        name: "Login",
      });
    },
    //查重
    async check() {
      const username = this.ruleForm.username;
      console.log(4444, username);
      const data = await this.$request.get("/reg/check/", {
        params: {
          username: username,
        },
      });
      console.log("传过来的数据", data.data);
      if (data.data.code === 0) {
        Toast.fail("亲,重名了");
        return;
      } else if (data.data.code === 1) {
        Toast.success();
      }
    },
    //注册
    async onSubmit(values) {
      const data = await this.$request.post("/reg/", {
        body: {
          username: values.username,
          password: values.password,
          headers: {
            "Content-Type": "application/json",
          },
        },
      });
      if (data.data.code === 1) {
        Toast.success("恭喜你注册成功!");
        this.ruleForm.username = "";
        this.ruleForm.password = "";
        this.$router.push({
          name: "Login",
        });
      }
    },
  },
};
</script>
