}
.top2 {
  width: 100%;
  position: absolute;
  // background-color: chartreuse;
  top: 20px;
  .btnjian {
    height: 24px;
    width: 24px;
    border-radius: 100%;
    border: 1px solid white;
    border: 1px solid white;
    background-color: rgba(226, 220, 220, 0);
